<template>
  <!-- the children components/objects of DbTable components -->
  <!-- access during evaluation -->
  <el-table
    :data="getTableData">
    <el-table-column
      v-for="item in getCols"
      :label="item.label"
      :prop="item.prop"
      width="180">
    </el-table-column>
  </el-table>
</template>

<script>
import Vue from 'vue'
import { TableColumn, Table } from 'element-ui'

Vue.use(TableColumn)
Vue.use(Table)

/* private instance fields & methods */

export default {
  name: 'DbTable',
  /* register the structures of the components */
  components: {},
  /* public interfaces; constructor parameters */
  props: {
    /* get the attributes of a table */
    col: [Array], // required type
    /* get the tuples of a table */
    row: [Array],
    /**
     * A list of column fields & names, from part of
     * the returning fields of the HTTP response body, mostly.
     * Furthermore, some of them need to be re-computed.
     */
    cols: {
      required: true,
      type: Object
    },
    tableData: {
      required: true,
      type: Array
    },
    /*
    * {
    *   row: {
    *     attr: 'val',
    *     attr: 'val1'
    *   }
    *   cell: {
    *     attr: 'val',
    *     attr: 'val1'
    *   }
    * }
    */
    herderStyle: [Object],
    bodyStyle: [Object]
  },
  /* lifecycle hooks */
  created () {

  },
  /* public instance methods */
  methods: {
  },
  /* template expressions */
  computed: {
    getTableData () { // a function to be called; when the property(s) is mutated; props, data properties
      return this.tableData
    },
    getCols () { // a function to be called; when the property(s) is mutated; props, data properties
      const props = this.cols.props
      const names = this.cols.names
      const tmp = {
        prop: '',
        name: ''
      }
      const arr = []

      if (props) { // the object exists
        // create a list of objects
        for (let i = 0; i < props.length; i++) {
          tmp.prop = props[i] // event sources: when th object is accessed/mutated
          tmp.name = names[i]

          arr.push(tmp) // listen data properties; event source

          console.log(this.cols)
        }
      }

      return arr
    }
  },
  /* watch data changes */
  /*
   * iterate & read the properties(dependency-tracking);
   * as event sources
   * listen props interfaces(change-notification)
   */
  watch: {
    tableData () { // a function to be called; when the property is accessed/mutated
      console.log('hi')
    }
  },
  /* public instance fields */
  data () {
    /* init the component fields */

    /* public ins fields */
    return {
      /* access data to init the component */
      colNameField: [],
      table: []
    }
  }
}
</script>

<!-- Mix the global & local component styles -->
<style>

</style>
<style scoped>

</style>



