<template>
  <!-- the child components/objects of the current component -->
  <div>
    <p>Original message: "{{ message }}"</p>
    <p>Computed reversed message: "{{ reversedMessage }}"</p>
    <a href="" @click.prevent="handleClick">click</a>
    <!-- accessing during evaluation -->
    <db-table :table-data="tableData" :cols="cols">
    </db-table>
  </div>
</template>

<script>
import DbTable from '@/components/DbTable'
import ajax from '@/libs/http/ajax' // default export; single function

/* private ins fields & methods */

export default {
  name: 'Home',
  components: {DbTable},
  created () {
    this.getBrand()
  },
  /* public instance methods */
  methods: {
    getBrand () {
      const handleSuppliers = resBody => { // concurrency process; handle response
        this.tableData = resBody.entry

        this.cols = {
          props: ['id', 'name'],
          names: ['供应商ID', '供应商名称']
        }
      }

      ajax('POST', this.URL.querySuppliers,
        {
          pageNo: 1,
          pageSize: 10
        },
        handleSuppliers)
    },
    handleClick () {
      this.message = 'world'
    }
  },
  computed: {
    // a computed getter
    reversedMessage: function () {
      // `this` points to the vm instance
      return this.message.split('').reverse().join('')
    }
  },
  watch: {
  },
  /* public instance fields */
  data () {
    return {
      message: 'Hello',
      URL: {
        querySuppliers: 'http://daily.qundian.elephtribe.com/api/procurement/wxhc/supplier/query'
      },
      tableData: [],
      cols: {}
    }
  }
}
</script>

<style scoped>

</style>



